<template>
    <div id="algo"> 
        <h1>Two way data binding (Enlace en dos direcciones)</h1>
        <input type="text" v-model="nombre">
        <h2>{{ nombre }}</h2>
        <hr>
        <button v-on:click="cambiar_valor">Cambiar Valor</button>
    </div>
</template>

<script>
export default {
    name: "Two-way-component",
    data: function () {
        return {
            nombre: ""
        }
    },
    methods:{
        cambiar_valor:function(){
        this.nombre = 'Cambie el valor';
      }
    }
}
</script>

<style>
#algo{
    background-color: rgba(16, 148, 249, 0.701);
}
</style>




